const new_id = () => list.length ? Math.max(...list.map((item) => item.id)) + 1 : 0;
// Svelteでは、配列を更新するときには、配列自体への参照を変更する必要があります。これは、Svelteが配列の変更を検出するために配列への参照の変更を監視しているからです。
const add_list = () => list = [...list, { id: new_id(), text: 'add', link: 'https://google.com', check: false }];
const insert_list = (idx) => list = [...list.slice(0, idx), { id: new_id(), text: 'insert', link: 'https://google.com', check: false }, ...list.slice(idx)];
const delete_list = (idx) => list = [...list.slice(0, idx), ...list.slice(idx + 1)];

sample.forEach(V=>{
